//контейнер
@mixin container($padding) {
  max-width: #{$maxWidth}px;
  width: 100%;
  padding: 0 #{$padding}px;
  margin: 0 auto;
}

//применение
//.container {
//@include container(15)

@mixin container2($max-width, $padding) {
  max-width: #{$max-width}px;
  width: 100%;
  padding: 0 #{$padding}px;
  margin: 0 auto;
}

//применение
