//медиа
@mixin xl() {
  @media (max-width: #{$xl}px) {
    @content;
  }
}
@mixin lg() {
  @media (max-width: #{$lg}px) {
    @content;
  }
}
@mixin md() {
  @media (max-width: #{$md}px) {
    @content;
  }
}
@mixin sm() {
  @media (max-width: #{$sm}px) {
    @content;
  }
}
@mixin xsm() {
  @media (max-width: #{$xsm}px) {
    @content;
  }
}

